@import url(https://fonts.googleapis.com/css?family=Alfa+Slab+One&display=swap);
body {
  margin: 0;
  padding: 0; }

.giphy__header {
  font-family: "Alfa Slab One", cursive;
  background: #2B2B2A;
  color: white;
  display: flex;
  justify-content: space-between;
  padding: 15px 20px;
  margin-bottom: 20px; }
  .giphy__header span {
    font-size: 25px; }
  .giphy__header button {
    padding: 9px 15px;
    background: green;
    box-shadow: none;
    border: none;
    color: white;
    border-bottom-right-radius: 10px;
    border-top-right-radius: 10px; }
  .giphy__header input {
    padding: 6px;
    border-bottom-left-radius: inherit;
    border-top-left-radius: inherit;
    border: none;
    box-shadow: none;
    border: 3px solid green; }
  .giphy__header .search {
    border-radius: 10px; }

body {
  margin: 0;
  padding: 0; }

.giphy__initial-text span {
  display: block;
  text-align: center;
  margin-bottom: 20px; }

.giphy__gifs {
  display: flex;
  flex-wrap: wrap; }

.giphy__details {
  margin: 40px 20px 10px;
  font-size: 24px; }

.giphy__navbtn {
  background: #C5C5C5;
  border: none;
  padding: 5px;
  border-radius: 5px;
  margin: 0 10px;
  cursor: pointer; }

.giphy__loader {
  display: block;
  text-align: center;
  font-size: 24px;
  margin: 20px; }

.giphy__gif {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 50px;
  box-shadow: 4px 4px 10px 0px #E9E9E9;
  padding: 5px 5px 15px;
  margin: 20px;
  cursor: default; }
  .giphy__gif:hover {
    box-shadow: 10px 10px 10px 0px #E9E9E9; }

.giphy__title, .giphy__copy-wrapper {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: inherit;
  text-align: center; }

.giphy__copy-wrapper {
  color: #767676;
  margin: 5px;
  cursor: pointer; }
  .giphy__copy-wrapper:hover {
    color: #101010; }

